/*-----------------------------------------*/
/* Header */
/*-----------------------------------------*/

.site-header {
   align-content: center;
   display: flex;
   flex-wrap: wrap;
   height: 100%;
   justify-content: center;
   padding: $base-space-unit * 2;

   @include tablet {
      align-items: center;
      width: 100%;
   }

   @include desktop {
      justify-content: flex-start;

      &.container {
         margin-left: 0;
         margin-right: 0;
      }
   }

   @include min-screen-height(400px) {
      // height: 90vh;
   }
}

.logo,
.search-form {
   display: block;
   width: 300px;

   @include tablet {
      max-width: 250px;
   }

   @include desktop {
      max-width: 300px;
   }
}

.logo {
   margin-bottom: $base-space-unit;
   text-align: center;
   transition: all 0.5s 0.5s ease-out;

   @include tablet {
      flex: 0 1 25%;
      margin: 0 $base-space-unit 0 0;
      text-align: left;
   }
}

.search-form {
   @include tablet {
      flex: 0 1 75%;
   }

   input {
      background-color: transparent;
      color: white;
      @include font-size(1.1);
      padding-left: $base-space-unit * 2;
      width: 100%;
   }

   select {
      @include font-size(1.1);
   }
}

.search-form-inner {
   display: inline-block;
   position: relative;
   text-align: center;
   margin-bottom: 1rem;
   width: 100%;

   @include tablet {
      text-align: left;
   }

   p {
      margin-bottom: 1rem;
   }
}
